.sidebar {
  background-color: #000;
  color: white;
  width: 19vw;
  max-width: 400px;
  position: fixed;
  top: 50px;
  right: 0;
  height: 100%;
  z-index: 1;
}

.sidebar__settings {
  overflow-y: auto;
  height: calc(100% - 450px);
}

.sidebar__sketch * {
  border-top: 1px solid white;
  height: 30%;
  position: fixed;
  display: flex;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  bottom: 0;
}
